html {
  box-sizing: border-box;
}

:root {
  --primary-color: hsl(0%, 0%, 0%);
  --second-color: #00acdb;
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.menu {
  justify-content: center;
  margin-right: 50px;
  display: flex;
}

.menu__link {
  color: var(--main-color);
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.menu__link:hover {
  color: var(--second-color);
}

.menu__item {
  text-transform: uppercase;
  text-align: center;
  margin-left: 20px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
